import { DateTime } from "luxon";
import { useEffect, useState } from "react";
import DatePicker from 'react-datepicker';
import 'react-datepicker/dist/react-datepicker.css';
import { useNavigate } from "react-router-dom";
import { getActivityCodes, homePageApi, myScheduleChanges, ptoBalances, viewNextScheduleChanges } from "../api/api";
import { HeaderLayout } from "../layout/header";


export const MyScheduleChanges: React.FC = () => {
  const navigate = useNavigate();
  const [selectedDate, setSelectedDate] = useState<any>(null);
  const [startTime, setStartTime] = useState<string | null>(null);
  const [endTime, setEndTime] = useState<string | null>(null);
  const [activityId, setActivity] = useState<string | null>(null);
  const [errorMessage, setErrorMessage] = useState<{ [key: string]: string }>({});
  const [ptoBalanceData, setPtoBalanceData] = useState<any | null>(null);
  const [activityList, setActivityList] = useState<any>({});

  const [formError, setFormError] = useState<string | null>(null);
  const [dateList, setDateList] = useState<string[]>([]);
  const [selectedScheduleData, setSelectedScheduleData] = useState([]);

  const handleActivityChange = (e: React.ChangeEvent<HTMLSelectElement>) => {
    setActivity(e.target.value);
  };

  useEffect(() => {
    const newDateFormat = DateTime.fromJSDate(selectedDate).toFormat("yyyy-MM-dd");
    const ptoBalancesData = {
      "reqId": "2341",
      agentOID: localStorage.getItem("agentOID"),
      "date": newDateFormat,
      "activityCodes": [activityId]
    }

    ptoBalances(ptoBalancesData).then(data => {
      if (data?.data?.result?.respCode === "200") {
        setPtoBalanceData(data?.data?.result?.['response']?.['agentPTOBalances']?.[0])
      }
    })
    getActivityCodes({ name: "actCodeType" }).then(data => {
      if (data?.data?.result?.respCode === "200") {
        const activities = data?.data?.result?.['response']
        // const activities:any = {
        //       "PublicHoliday": "8ade84c079d11bde017a32bee3a300aa",
        //       "Childcare Leave": "8ade84c079d11bde017a32f58aef010f",
        //       "Annual Leave": "8ade84c079d11bde017a32f2bc2f010c",
        //       "Flexi Leave": "8ade84c079d11bde017a32c8c8e600cb"
        //     }
        let newActivity: any = {}
        Object.keys(activities)?.forEach(activity => (
          newActivity[activities?.[activity]] = activity
        ))
        setActivityList(newActivity)
      }
    })
    viewNextScheduleChanges({
      agentOID: localStorage.getItem("agentOID"),
      startDate: DateTime.now().startOf('week').toFormat("yyyy-MM-dd"),
      endDate: DateTime.now().startOf('week').plus({ days: 49 }).toFormat("yyyy-MM-dd"),
      modifiedSince: "",
      includeNotes: "true",
    }).then(data => {
      if (data?.data?.result?.respCode === "200") {
        const data1 = data?.data?.result?.response?.getAgentSchedulesV2Response?.return?.baseSchedule
        let dateGroup: any = []
        data1?.forEach((response: any) => {
          dateGroup.push(response?.scheduleDate)
        })
        setDateList(dateGroup)
      }
    })
  }, [])

  useEffect(() => {
    if (!activityId) return
    if (!selectedScheduleData?.length) return
    const filterData = selectedScheduleData?.find((scheduleData: any) => scheduleData?.actCodeOID === activityId) as any

    if (filterData) {
      const end = DateTime.fromISO(filterData?.endDateTime).toFormat('HH:mm')
      const start = DateTime.fromISO(filterData?.startDateTime).toFormat('HH:mm')
      setEndTime(end)
      setStartTime(start)
    } else {
      setEndTime(null)
      setStartTime(null)
    }
  }, [selectedScheduleData, activityId])


  useEffect(() => {
    if (!selectedDate) return
    const selectedDateFormat = DateTime.fromJSDate(selectedDate).toFormat('yyyy-MM-dd')
    getSingleDayData(selectedDateFormat)
  }, [selectedDate])

  const getSingleDayData = (date: any) => {
    setSelectedScheduleData([])
    homePageApi({
      agentOID: localStorage.getItem("agentOID"),
      startDate: date,
      endDate: date,
      modifiedSince: "",
      includeNotes: 'true',
    }).then((data) => {
      if (data?.data?.result?.respCode === "200") {
        const data1 = data?.data?.result?.response;
        setSelectedScheduleData(
          data1?.getAgentSchedulesV2Response?.return?.baseSchedule?.scheduleDetail as any
        );
      }
    });
  }

  const onSubmit = (e: any) => {
    e.preventDefault();
    setFormError(null)
    setErrorMessage({})
    const endTime = e.target.elements?.endTime?.value
    const startTime = e.target.elements?.startTime?.value
    const date = e.target.elements?.date?.value;
    if (!activityId) {
      setErrorMessage(prev => ({ ...prev, activityId: "Please select Activity" }))
    }
    if (date === "") {
      setErrorMessage(prev => ({ ...prev, date: "Please select Date" }))
    }
    if (startTime === "") {
      setFormError("Not Allow to submit form")
      // setErrorMessage(prev => ({ ...prev, startTime: "Please select start time" }))
    }
    if (endTime === "") {
      setFormError("Not Allow to submit form")
      // setErrorMessage(prev => ({ ...prev, endTime: "Please select end time" }))
    }

    const newDateFormat = DateTime.fromJSDate(selectedDate).toFormat("yyyy-MM-dd");
    if (activityId !== "" && newDateFormat !== "" && endTime !== "" && startTime !== "") {
      const startDateTime = `${newDateFormat}T${startTime}:00Z`;
      const endDateTime = `${newDateFormat}T${endTime}:00Z`;
      const myScheduleChange = {
        agentOID: localStorage.getItem("agentOID"),
        "scheduleDate": newDateFormat,
        "note": {
          "allowAgentToView": "true",
          "text": "schedule note added"
        },
        "scheduleDetails": {
          "actCodeOID": activityId,
          "endDateTime": endDateTime,
          "note": {
            "allowAgentToView": "true",
            "text": "schedule text replaced"
          },
          "startDateTime": startDateTime
        }
      }
      const ptoBalancesData = {
        "reqId": "2341",
        agentOID: localStorage.getItem("agentOID"),
        "date": newDateFormat,
        "activityCodes": [activityId]
      }

      ptoBalances(ptoBalancesData).then(ptoData => {
        const groupAllotmentBalance = ptoData?.data?.result?.['response']?.['agentPTOBalances']?.[0]?.['groupAllotment']?.['balance'];
        if (groupAllotmentBalance > 9) {
          myScheduleChanges(myScheduleChange).then(data => {
            if (data?.data?.result?.respCode === "200") {
              const data1 = data?.data?.result?.response
              navigate("/home");
            } else {
              setFormError(data?.data?.[0]?.respCode)
            }
          }).catch(error => {
            setFormError(error)
          })
        } else {
          setFormError("Group Allotment must greater than 9")
        }
      })
    }
  };

  const isDateDisabled = (date: any) => {
    const disabledDate = DateTime.fromJSDate(date).toFormat('yyyy-MM-dd')
    return !dateList.includes(disabledDate) && DateTime.fromJSDate(date) > DateTime.now();
  };

  const calculatePtoBalanceTime = (time: string) => {
    if (time) {
      const newTime = time.toString().split('.')
      const changedTime = newTime?.[0] + ":" + ((Number(newTime?.[1]) * 60) / 100).toFixed(0)
      return changedTime + "  " + activityList?.[activityId || ""]?.value
    }
    return ""
  }

  return (
    <>
      <HeaderLayout />
      <div className="container">
        <div className="row">
          <div className="col-md-4 col-sm-4 margin-0 mt-50">
            {/* <div class="mb-4 align-items-center text-center mb-4">
      <img id="profile-img" class="profile-img-card logo" src="images/singtel_logo_coloured.png" alt="Logo Image" />
    </div> */}
            <div className="clearfix" />
            <div className="mb-4 align-items-center text-center mt-2">
              <p>
                <span>John Doe</span> login on <span>13 June 2024, 17:24</span>{" "}
              </p>
            </div>
            <form className="border p-4 card" onSubmit={onSubmit}>
              <div className="form-group mb-3 dropdown">
                <label htmlFor="exampleInputEmail1">Activity </label>
                <select
                  name="activity"
                  value={activityId || ""}
                  onChange={handleActivityChange}
                  className="custom-select form-control"
                  id=""
                >
                  <option>Select Activity</option>
                  {
                    Object.keys(activityList)?.map(activity => (
                      <option key={activityList?.[activity]} value={activity}>{activityList?.[activity]}</option>
                    ))
                  }
                </select>
                <div hidden={!errorMessage?.activityId} className="invalid-feedback">
                  {errorMessage?.activityId}
                </div>
              </div>
              {
                ptoBalanceData?.balances?.[activityId || ""] ? (
                  <div className="mb-1 align-right font-12"><span className="star align-right">{calculatePtoBalanceTime(ptoBalanceData?.balances?.[activityId || ""]?.['balance'])}</span>&nbsp;&nbsp;&nbsp;</div>
                ) : null
              }
              <div className="form-group mb-3">
                <label htmlFor="exampleInputEmail1">Date </label>
                <br />
                <DatePicker
                  className="form-control"
                  name="date"
                  dateFormat="dd-MM-yyyy"
                  selected={selectedDate}
                  onChange={(date) => setSelectedDate(date)}
                  filterDate={isDateDisabled}
                />
                {/* <input
                  type="date"
                  name="date"
                  className="form-control"
                  id="exampleInputEmail1"
                  aria-describedby="emailHelp"
                  placeholder="Enter email"
                /> */}
                <div hidden={!errorMessage?.date} className="invalid-feedback">
                  {errorMessage?.date}
                </div>
              </div>
              <div className="form-group mb-3 dropdown">
                <label htmlFor="exampleInputEmail1">Start Time </label>
                <input
                  type="time"
                  name="startTime"
                  onChange={(e) => setStartTime(e.target.value)}
                  value={startTime || ""}
                  className="form-control"
                  aria-describedby="emailHelp"
                  placeholder="Enter start time"
                  readOnly
                />
                {/* <select
                  name="startTime"
                  className="custom-select form-control"
                  id=""
                  value={startTime}
                  onChange={handleStartTimeChange}
                >
                  <option>Select Start Time</option>
                  <option value={1}>8:00 AM</option>
                  <option value={2}>9:00 AM</option>
                  <option value={3}>10:00 AM</option>
                  <option value={4}>11:00 AM</option>
                  <option value={5}>12:00 AM</option>
                  <option value={6}>1:00 PM</option>
                  <option value={7}>2:00 PM</option>
                  <option value={8}>3:00 PM</option>
                  <option value={9}>4:00 PM</option>
                  <option value={10}>5:00 PM</option>
                  <option value={11}>6:00 PM</option>
                </select> */}
                <div hidden={!errorMessage?.startTime} className="invalid-feedback">
                  {errorMessage?.startTime}
                </div>
              </div>
              <div className="form-group mb-3 dropdown">
                <label htmlFor="exampleInputEmail1">End Time </label>
                <input
                  type="time"
                  name="endTime"
                  value={endTime || ""}
                  readOnly
                  onChange={(e) => setEndTime(e.target.value)}
                  className="form-control"
                  aria-describedby="emailHelp"
                  placeholder="Enter end time"
                />
                {/* <select
                  value={endTime}
                  onChange={handleEndTimeChange}
                  className="custom-select form-control"
                  id=""
                >
                  <option>Select Start Time</option>
                  <option value={1}>8:00 AM</option>
                  <option value={2}>9:00 AM</option>
                  <option value={3}>10:00 AM</option>
                  <option value={4}>11:00 AM</option>
                  <option value={5}>12:00 AM</option>
                  <option value={6}>1:00 PM</option>
                  <option value={7}>2:00 PM</option>
                  <option value={8}>3:00 PM</option>
                  <option value={9}>4:00 PM</option>
                  <option value={10}>5:00 PM</option>
                  <option value={11}>6:00 PM</option>
                </select> */}
                <div hidden={!errorMessage?.endTime} className="invalid-feedback">
                  {errorMessage?.endTime}
                </div>
              </div>
              <div className="d-flex d-lg-block">
                <button
                  type="submit"
                  className="btn btn-primary col-12 button-color"
                >
                  Submit
                </button>
              </div>
              <div hidden={!formError} className="invalid-feedback">
                {formError?.toString()}
              </div>
            </form>
          </div>
        </div>
      </div>
    </>
  );
};
