import { useState } from "react";
import { useNavigate } from "react-router-dom";
import { getAuthentication, loginApi } from "../api/api";
// import * as jwt from 'jsonwebtoken';

// const secretKey = 'your_secret_key_here';

export const LoginPage: React.FC = () => {
  const [formError, setFormError] = useState<string | null>(null);
  const [errorMessage, setErrorMessage] = useState<{ [key: string]: string }>({});
  const navigate = useNavigate()

  const onSubmit = (e: any) => {
    e.preventDefault();
    const userName = e.target.elements.userName.value
    const password = e.target.elements.password.value
    setErrorMessage({})

    const payload = { userName, password };
    // const jwtToken =  jwt.sign(payload, secretKey, { expiresIn: '1h' }); // Token expires in 1 hour
    // console.log(jwtToken)
    if (userName === "") {
      setErrorMessage(prev => ({ ...prev, name: "Please enter the valid Username" }))
    }
    if (password === "") {
      setErrorMessage(prev => ({ ...prev, password: "Please enter the valid password" }))
    }

    if (userName !== "" && password !== "") {
      loginApi({ userName, password }).then((data: any) => {
        if (data?.data?.result?.respCode === "200") {
          const loginData = data?.data?.result?.response
          console.log(loginData, "login success response")
          localStorage.setItem('agentOID', loginData?.agentOID)
          localStorage.setItem('restServiceSessionId', loginData?.jsession)
          getAuthentication({ agentOID: loginData?.agentOID }).then((data: any) => {
            if (data?.data?.result?.respCode === "200") {
              const auth = data?.data?.result?.response
              console.log(auth, "login getAuthentication success response")
              localStorage.setItem('wfmCookie', auth?.jsessionID)
              console.warn(data?.data, "login success")
              navigate('/home')
            } else {
              setFormError(data?.data?.[0]?.respCode)
            }
          }).catch(error => {
            console.error(error, "login getAuthentication error response")
            setFormError(error)
          })
        } else {
          console.error(data, "login error response")
          setFormError(data?.data?.[0]?.respCode)
        }
      }).catch(error => {
        console.error(error, "login cache error response")
        if (error.toString() === "AxiosError: Network Error") {
          setFormError("Network Error")
        } else {
          setFormError(error)
        }
      })
    }
  }

  return (
    <div className="row">
      <div className="col-md-4 col-sm-4 margin-0 mt-200">
        <div className="mb-4 align-items-center text-center">
          <img
            id="profile-img"
            className="profile-img-card logo"
            src="images/singtel_logo_coloured.png"
            alt="Logo Image"
          />
        </div>
        <form className="border p-4 card" onSubmit={onSubmit}>
          <div className="form-group mb-3">
            <label htmlFor="username-input">
              Username (LAN ID) <span className="star">*</span>{" "}
            </label>
            <input
              type="text"
              name="userName"
              className="form-control"
              id="username-input"
              aria-describedby="username"
              placeholder="Enter Username (LAN ID)"
            />
            <div hidden={!errorMessage?.name} className="invalid-feedback">{errorMessage?.name}</div>
          </div>
          <div className="form-group mb-4">
            <label htmlFor="exampleInputPassword1">
              Password <span className="star">*</span>
            </label>
            <input
              type="password"
              name="password"
              className="form-control"
              id="exampleInputPassword1"
              placeholder="Password"
            />
            <div hidden={!errorMessage?.password} className="invalid-feedback">{errorMessage?.password}</div>
          </div>
          <div className="d-flex d-lg-block">
            <button
              type="submit"
              className="button-color function-button login-btn"
            >
              Login
            </button>
          </div>
          <div hidden={!formError} className="invalid-feedback">{formError?.toString()}</div>
        </form>
      </div>
    </div>
  )
}