import { DateTime } from "luxon";
import { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import { homePageApi, viewNextScheduleChanges } from "../api/api";
import { HeaderLayout } from "../layout/header";

export const HomePage: React.FC = () => {
  const navigate = useNavigate();
  const [todayScheduleData, setTodayScheduleData] = useState(null);
  const [nextSixDaysScheduleData, setNextSixDaysScheduleData] =
    useState<any>(null);
  const [isModelOpen, setIsModelOpen] = useState<boolean>(false);
  const [selectedScheduleData, setSelectedScheduleData] = useState(null);


  useEffect(() => {
    homePageApi({
      agentOID: localStorage.getItem("agentOID"),
      startDate: DateTime.now().toFormat("yyyy-MM-dd"),
      endDate: DateTime.now().toFormat("yyyy-MM-dd"),
      modifiedSince: "",
      includeNotes: "true",
    }).then((data) => {
      console.warn(data, "home page get single data success response initially")
      if (data?.data?.result?.respCode === "200") {
        const data1 = data?.data?.result?.response;
        setTodayScheduleData(
          data1?.getAgentSchedulesV2Response?.return?.baseSchedule as any
        );
      }
    }).catch(error => {
      console.error(error, "home page get single data error response initially")
    })

    viewNextScheduleChanges({
      agentOID: localStorage.getItem("agentOID"),
      startDate: DateTime.now().startOf('week').toFormat("yyyy-MM-dd"),
      endDate: DateTime.now().endOf('week').toFormat("yyyy-MM-dd"),
      modifiedSince: "",
      includeNotes: "true",
    }).then((data) => {
      console.warn(data, "home page get viewNextScheduleChanges success response initially")
      if (data?.data?.result?.respCode === "200") {
        const scheduleData = data?.data?.result?.response;
        setNextSixDaysScheduleData(scheduleData?.getAgentSchedulesV2Response?.return?.baseSchedule as any);
      }
    }).catch(error => {
      console.error(error, "home page get viewNextScheduleChanges error response initially")
    })
  }, []);


  const getSingleDayData = (date: any) => {
    setIsModelOpen(true)
    homePageApi({
      agentOID: localStorage.getItem("agentOID"),
      startDate: date,
      endDate: date,
      modifiedSince: "",
      includeNotes: "true",
    }).then((data) => {
      if (data?.data?.result?.respCode === "200") {
        console.warn(data, "home page get single data success response")
        const data1 = data?.data?.result?.response;
        setSelectedScheduleData(
          data1?.getAgentSchedulesV2Response?.return?.baseSchedule as any
        );
      }
    }).catch(error => {
      console.error(error, "home page get single data error response")
    })
  }


  return (
    <>
      <HeaderLayout isShowBackButton={false} />
      <div className="container mt-5">
        <div className="row">
          <h3 className="purple-color mb-3">Welcome to Your Schedule Page!</h3>
          <h5 className="purple-color">
            <strong>
              Today's Schedule -{" "}
              {DateTime.now().toLocaleString({ weekday: "long" }).toString()},{" "}
              {DateTime.now().toFormat("dd MMMM yyyy")}
            </strong>
          </h5>
          <div className="margin-10 table-responsive">
            <table className="table border mt-2 mb-3" id="schedule">
              <thead>
                <tr>
                  <th>Day</th>
                  <th>Schedule</th>
                  <th>Activity</th>
                  <th>Start Time</th>
                  <th>End Time</th>
                </tr>
              </thead>
              <tbody>
                <tr>
                  <td>
                    {DateTime.now()
                      .toLocaleString({ weekday: "short" })
                      .toString()}{" "}
                    {DateTime.now().toFormat("dd-MM-yyyy")}
                  </td>
                  <td>
                    {todayScheduleData ? DateTime.fromISO(
                      (todayScheduleData as any)?.startDateTime
                    ).toFormat("hh:mm a") : null}{" "}
                    -{" "}
                    {todayScheduleData ? DateTime.fromISO(
                      (todayScheduleData as any)?.endDateTime
                    ).toFormat("hh:mm a") : null}
                  </td>
                  <td>{(todayScheduleData as any)?.actCodeOID ?? "-"}</td>
                  <td>
                    {todayScheduleData ? DateTime.fromISO(
                      (todayScheduleData as any)?.startDateTime
                    ).toFormat("hh:mm a") : "-"}
                  </td>
                  <td>
                    {todayScheduleData ? DateTime.fromISO(
                      (todayScheduleData as any)?.endDateTime
                    ).toFormat("hh:mm a") : "-"}
                  </td>
                </tr>
                {((todayScheduleData as any)?.scheduleDetail as [])?.map(
                  (data: any) => (
                    <tr>
                      <td colSpan={2} />
                      <td>{data?.actCodeOID}</td>
                      <td>
                        {DateTime.fromISO(data?.startDateTime).toFormat(
                          "hh:mm a"
                        )}
                      </td>
                      <td>
                        {DateTime.fromISO(data?.endDateTime).toFormat(
                          "hh:mm a"
                        )}
                      </td>
                    </tr>
                  )
                )}
              </tbody>
            </table>
          </div>
          <div className="clearfix" />
          <div className="mt-3" />
          <h5 className="purple-color">
            <strong>Current Week Data</strong>
          </h5>
          <div className="margin-10 table-responsive">
            <table className="table border mt-2 mb-3" id="">
              <thead>
                <tr>
                  <th>Date</th>
                  <th>Time</th>
                </tr>
              </thead>
              <tbody>
                {nextSixDaysScheduleData?.map((data: any) => (
                  <tr className="">
                    <td>
                      {DateTime.fromISO(
                        data?.scheduleDate
                      )
                        .toLocaleString({ weekday: "short" })
                        .toString()}
                      {DateTime.fromISO(
                        data?.scheduleDate
                      ).toFormat("dd-MM-yyyy")}
                    </td>

                    <td>
                      <button
                        onClick={() => {
                          getSingleDayData(data?.scheduleDate)
                        }}
                        className="popup-button"
                        data-bs-toggle="modal"
                        data-bs-target="#myModal"
                      >
                        {data?.startDateTime?DateTime?.fromISO(
                          data?.startDateTime
                        )?.toFormat("hh:mm a"):"-"}
                        {"-"}
                        {data?.endDateTime?DateTime?.fromISO(
                          data?.endDateTime
                        )?.toFormat("hh:mm a"):"-"}
                      </button>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        </div>
      </div>
      <div className="row">
        <div className="text-center">
          <a onClick={() => navigate("/my-schedule-changes")}>
            <button
              type="button"
              className="btn btn-secondary col-md-5 col-sm-5 col-xs-12 m-2 button-color function-button"
            >
              My Schedule Changes
            </button>
          </a>
          <a onClick={() => navigate("/agent-time-off-day")}>
            <button
              type="button"
              className="btn btn-primary col-md-5 col-sm-5 m-2 col-xs-12 m-2 button-color function-button"
            >
              Time Off Manager
            </button>
          </a>
        </div>
        <div className="text-center">
          <a onClick={() => navigate("/weekly-schedule")}>
            <button
              type="button"
              className="btn btn-secondary col-md-5 col-sm-5 m-2 col-xs-12 button-color function-button"
            >
              View Weekly Schedule
            </button>
          </a>
          <a onClick={() => navigate("/agent-request-list")}>
            <button
              type="button"
              className="btn btn-primary col-md-5 col-sm-5 m-2 col-xs-12 button-color function-button"
            >
              Request Details
            </button>
          </a>
        </div>
      </div>

      <div className={`modal ${isModelOpen ? "show-model" : ""}`} id="myModal">
        <div className="modal-dialog modal-lg show">
          <div className="modal-content">
            {/* Modal Header */}
            <div className="modal-header">
              <h4 className="modal-title">Day Activity Details</h4>
              <button
                onClick={() => setIsModelOpen(false)}
                type="button"
                className="btn-close"
                data-bs-dismiss="modal"
              />
            </div>
            {/* Modal body */}
            <div className="modal-body">
              <div className="table-responsive">
                <table className="table activity-details">
                  <thead>
                    <tr className="">
                      <th>Activity</th>
                      <th>Start Time</th>
                      <th>End Time</th>
                    </tr>
                  </thead>
                  <tbody>
                    {
                      ((selectedScheduleData as any)?.scheduleDetail || [])?.map((todaySchedule: any) => (
                        <tr>
                          <td>{todaySchedule?.actCodeOID}</td>
                          <td>{DateTime.fromISO((todaySchedule as any)?.startDateTime).toFormat('hh:mm a')}</td>
                          <td>{DateTime.fromISO((todaySchedule as any)?.endDateTime).toFormat('hh:mm a')}</td>
                        </tr>
                      ))
                    }
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};
