import { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import { requestInformationApi } from "../api/api";
import { HeaderLayout } from "../layout/header";

export const AgentRequestList = () => {
  const navigate = useNavigate();
  const [requestInformation, setRequestInformation] = useState<any>();

  useEffect(() => {
    const params = {
      agentOID: localStorage.getItem("agentOID"),
    }

    requestInformationApi(params).then(data => {
      if (data?.data?.[0]?.respCode === "200") {
        const data1 = data?.data?.[1]?.response;
        setRequestInformation(data1?.agentPTOBalances);
      }
    });
  }, []);


  return (
    <>
      <HeaderLayout />
      <div className="container mt-5">
        <div className="row">
          <h5 className="purple-color margin-30">
            <strong>Request Information </strong>
          </h5>
          {requestInformation?.map((info: any) => (
            <>
              <div className="margin-10">
                <table className="table border mt-2 mb-3 table-responsive agent-request">
                  <thead>
                    <tr>
                      <th scope="col">Request Type</th>
                      <th scope="col">{info.reqId}</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr>
                      <td>Date of Request Raised</td>
                      <td>{info.date}</td>
                    </tr>
                    <tr>
                      <td>Status</td>
                      <td>{info.status}</td>
                    </tr>
                    <tr>
                      <td>Reason</td>
                      <td>{info.reason}</td>
                    </tr>
                  </tbody>
                </table>
              </div>
              <div className="clearfix" />
              <div className="mt-3" />
            </>
          ))}
        </div>
      </div>
    </>
  );
};
