import { useState } from "react";
import { useNavigate } from "react-router-dom";

import { HeaderLayout } from "../layout/header";
import { activitiesList } from "../constrain";

export const AgentTimeOffDay = () => {
  const navigate = useNavigate();

  const [typeOfTimeOffError, setTypeOfTimeOffError] = useState<boolean>(false);
  const [startDateError, setStartDateError] = useState<boolean>(false);
  const [endDateError, setEndDateError] = useState<boolean>(false);
  const [formError, setFormError] = useState<boolean>(false);
  const [typeOfTimeOff, setTypeOfTimeOff] = useState<string>("");

  const handleTypeOfTimeOffChange = (
    e: React.ChangeEvent<HTMLSelectElement>
  ) => {
    setTypeOfTimeOff(e.target.value);
  };

  const onSubmit = (e: any) => {
    e.preventDefault();

    // const typeOfTimeOff = e.target.elements.typeOfTimeOff.value;
    const startDate = e.target.elements.startDate.value;
    const endDate = e.target.elements.endDate.value;

    if (typeOfTimeOff === "") {
      setTypeOfTimeOffError(true);
    } else {
      setTypeOfTimeOffError(false);
    }

    if (startDate === "") {
      setStartDateError(true);
    } else {
      setStartDateError(false);
    }

    if (endDate === "") {
      setEndDateError(true);
    } else {
      setEndDateError(false);
    }

    if (typeOfTimeOff !== "" && startDate !== "" && endDate !== "") {
      setFormError(true);
      navigate('/home')
    }
  };

  return (
    <>
      <HeaderLayout />
      <div className="container">
        <div className="row">
          <div className="col-md-4 col-sm-4 margin-0 mt-50">
            {/* <div class="mb-4 align-items-center text-center mb-4">
      <img id="profile-img" class="profile-img-card logo" src="images/singtel_logo_coloured.png" alt="Logo Image" />
    </div> */}
            <div className="clearfix" />
            <div className="mb-4 align-items-center text-center mt-2">
              <p>
                <span>John Doe</span> login <span>13 June 2024, 17:24</span>{" "}
              </p>
            </div>
            <form className="border p-4 card" onSubmit={onSubmit}>
              <div className="form-group mb-1 dropdown">
                <label htmlFor="exampleInputEmail1">Type of Time Off </label>
                <select
                  className="custom-select form-control"
                  name="typeOfTimeOff"
                  id=""
                  value={typeOfTimeOff}
                  onChange={handleTypeOfTimeOffChange}
                >
                  <option>Select Activity</option>
                  {
                    activitiesList?.map(activity => (
                      <option value={activity?.key}>{activity?.value}</option>
                    ))
                  }
                </select>
                <div hidden={!typeOfTimeOffError} className="invalid-feedback">
                  Please select Type of Time Off
                </div>
              </div>
              <div className="mb-1 align-right font-12">
                <span className="star align-right">190:30</span>
                &nbsp;&nbsp;&nbsp; Public holiday available{" "}
              </div>
              {/* <div class="form-group mb-3 dropdown">
            <label for="exampleInputEmail1">Length</label>
            <select class="custom-select form-control" id="">
              <option selected>Choose Length</option>
              <option value="1">Full Day</option>
              <option value="2">Partial Day</option>
              <option value="3">Week</option>
            </select>
           
            <div class="invalid-feedback">Length of time off is not selected</div>
          </div> */}
              <div className="form-group mb-3">
                <label htmlFor="exampleInputEmail1">Start Date </label>
                <input
                  type="date"
                  name="startDate"
                  className="form-control"
                  id="exampleInputEmail1"
                  aria-describedby="emailHelp"
                  placeholder="Enter email"
                />
                <div hidden={!startDateError} className="invalid-feedback">
                  Please enter the Start Date
                </div>
              </div>
              <div className="form-group mb-3">
                <label htmlFor="exampleInputEmail1">End Date </label>
                <input
                  type="date"
                  name="endDate"
                  className="form-control"
                  id="exampleInputEmail1"
                  aria-describedby="emailHelp"
                  placeholder="Enter email"
                />
                <div hidden={!endDateError} className="invalid-feedback">
                  Please enter the End Date
                </div>
              </div>

              {/* <div className="mb-3" hidden={!formError}>
                * You can leave the end date blank to request a single day.
              </div> */}
              <div className="d-flex d-lg-block">
                <button
                  type="submit"
                  className="btn btn-primary col-12 button-color"
                  data-bs-toggle="modal"
                  data-bs-target="#myModal"
                >
                  Submit
                </button>
                {/* <div class="invalid-feedback">Error Message</div> */}
              </div>
            </form>
          </div>
        </div>
      </div>
    </>
  );
};
