import { useNavigate } from "react-router-dom";
import { logoutApi } from "../api/api";

export const HeaderLayout = ({ isShowBackButton = true }: any) => {
  const navigate = useNavigate();

  const onSubmit = () => {
    const sessionId = localStorage.getItem('wfmCookie');
    logoutApi({ sessionId }).then(data => {
      if (data?.data?.result?.respCode === "200") {
        localStorage.removeItem('restServiceSessionId')
        localStorage.removeItem('agentOID')
        localStorage.removeItem('wfmCookie')
        navigate("/login");
      }
    })
  };

  return (
    <div className="container-fluid mt-2">
      <div className="row justify-content-between">
        <div className="col-4">
          <a
            onClick={() => navigate("/home")}
            hidden={!isShowBackButton}
            style={{ cursor: "pointer" }}
          >
            <img className="small" src="images/back.png" /> Back
          </a>
        </div>
        <div className="col-4 align-right">
          <a onClick={onSubmit} style={{ cursor: "pointer" }}>
            Exit <img className="small" src="images/exit.png" />
          </a>
        </div>
      </div>
    </div>
  );
};
