import { DateTime } from "luxon";
import { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import { homePageApi, viewNextScheduleChanges } from "../api/api";
import { HeaderLayout } from "../layout/header";

export const WeeklySchedule: React.FC = () => {
    const navigate = useNavigate();
    const [isModelOpen, setIsModelOpen] = useState<boolean>(false)
    const [scheduleData, setScheduleData] = useState<any[]>([])
    const [weeklyDataKey, setWeeklyScheduleData] = useState([])
    const startDate = DateTime.now().startOf('week');
    const endDate = DateTime.now().startOf('week').plus({ days: 42 });
    const [selectedScheduleData, setSelectedScheduleData] = useState(null);
    const WeekDays = []
    let WeekDay = []

    for (let day = startDate; day < endDate; day = day.plus({ days: 1 })) {
        if (DateTime.fromISO((day as any)).toLocaleString({ weekday: "short" }).toString() === "Mon") {
            WeekDay = []
        }
        WeekDay.push(day)
        if (DateTime.fromISO((day as any)).toLocaleString({ weekday: "short" }).toString() === "Sat") {
            WeekDays.push(WeekDay);
        }
    }

    useEffect(() => {
        const data = viewNextScheduleChanges({
            agentOID: localStorage.getItem("agentOID"),
            startDate: DateTime.now().startOf('week').toFormat("yyyy-MM-dd"),
            endDate: DateTime.now().startOf('week').plus({ days: 42 }).toFormat("yyyy-MM-dd"),
            modifiedSince: "",
            includeNotes: "true",
        }).then(data => {
            if (data?.data?.result?.respCode === "200") {
                const data1 = data?.data?.result?.response?.getAgentSchedulesV2Response?.return?.baseSchedule
                let dateGroup: any = {}
                data1?.map((response: any) => (
                    dateGroup[response?.scheduleDate] = response
                ))
                setWeeklyScheduleData(dateGroup)
            }
        })
    }, [])

    const getSingleDayData = (date: any) => {
        setIsModelOpen(true)
        homePageApi({
            agentOID: localStorage.getItem("agentOID"),
            startDate: date,
            endDate: date,
            modifiedSince: "",
            includeNotes: "true",
        }).then((data) => {
            if (data?.data?.result?.respCode === "200") {
                const data1 = data?.data?.result?.response;
                setSelectedScheduleData(
                    data1?.getAgentSchedulesV2Response?.return?.baseSchedule as any
                );
            }
        });
    }

    return (
        <>
            <HeaderLayout />
            <div className="container mt-5">
                <div className="row">
                    <div className="clearfix" />
                    <div className="mt-3" />
                    <h5 className="purple-color">
                        <strong>Next 6 Weeks Schedules </strong>
                    </h5>
                    {
                        WeekDays?.map((WeekDay: any) => (
                            <div className="margin-10">
                                <table className="table border mt-2 mb-3 schedule">
                                    <thead>
                                        <tr>
                                            <th scope="col">Date</th>
                                            <th scope="col">Time</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {
                                            WeekDay?.map((week: any) => (
                                                <tr>
                                                    <td>{DateTime.fromISO((week as any)).toLocaleString({ weekday: "short" }).toString()} {DateTime.fromISO((week as any)).toFormat('dd-MM-yyyy')}</td>
                                                    <td>
                                                        {
                                                            weeklyDataKey?.[week?.toISODate()] ? (
                                                                <button
                                                                    onClick={() => {
                                                                        getSingleDayData(week?.toISODate())
                                                                        // setScheduleData((weeklyDataKey?.[week?.toISODate()] as any)?.scheduleDetail)
                                                                    }}
                                                                    className="popup-button"
                                                                    data-bs-toggle="modal"
                                                                    data-bs-target="#myModal"
                                                                >
                                                                    {" "}
                                                                    {DateTime.fromISO((weeklyDataKey?.[week?.toISODate()] as any)?.startDateTime).toFormat(
                                                                        "hh:mm a"
                                                                    )} &nbsp;- &nbsp;
                                                                    {DateTime.fromISO((weeklyDataKey?.[week?.toISODate()] as any)?.endDateTime).toFormat(
                                                                        "hh:mm a"
                                                                    )}
                                                                </button>
                                                            ) : "OFF"
                                                        }

                                                    </td>
                                                </tr>
                                            ))
                                        }

                                    </tbody>
                                </table>
                            </div>
                        ))
                    }
                </div>
            </div>
            <div className={`modal ${isModelOpen ? "show-model" : ""}`} id="myModal">
                <div className="modal-dialog modal-lg show">
                    <div className="modal-content">
                        {/* Modal Header */}
                        <div className="modal-header">
                            <h4 className="modal-title">Day Activity Details</h4>
                            <button onClick={() => setIsModelOpen(false)} type="button" className="btn-close" data-bs-dismiss="modal" />
                        </div>
                        {/* Modal body */}
                        <div className="modal-body">
                            <div className="table-responsive">
                                <table className="table activity-details">
                                    <thead>
                                        <tr className="">
                                            <th>Activity</th>
                                            <th>Start Time</th>
                                            <th>End Time</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {
                                            ((selectedScheduleData as any)?.scheduleDetail || [])?.map((todaySchedule: any) => (
                                                <tr>
                                                    <td>{todaySchedule?.actCodeOID}</td>
                                                    <td>{DateTime.fromISO((todaySchedule as any)?.startDateTime).toFormat('hh:mm a')}</td>
                                                    <td>{DateTime.fromISO((todaySchedule as any)?.endDateTime).toFormat('hh:mm a')}</td>
                                                </tr>
                                            ))
                                        }
                                    </tbody>
                                </table>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>

    )
}