import axios from "axios";

export const loginApi = async (data: any) => {
  // return await fetch("https://bdd5-111-65-38-117.ngrok-free.app/WFMMWIntegration/users/wfmLogin",
  //   {
  //     method:"post",
  //     body: JSON.stringify({
  //       logonId: data?.userName,
  //       logonpwd: data?.password,
  //     }),
  //   }
  // );
  return await axios({
    method: "post",
    url: "users/wfmLogin",
    data: {
      logonId: data?.userName,
      logonpwd: data?.password,
    },
  });
};

export const LoginCheckApi = async () => {
  const agentOID = localStorage.getItem("agentOID");
  return await axios({
    method: "post",
    url: "services/getAuthentication",
    data: {
      agentOID: agentOID,
    },
  });
};

export const getAuthentication = async (data: any) => {
  return await axios({
    method: "post",
    url: "users/getAuthentication",
    data: {
      agentOID: data?.agentOID,
    },
  });
};

