axios.interceptors.request.use(
  config => {
    const wfmCookie = localStorage.getItem("wfmCookie");
    console.log(wfmCookie)
    if (wfmCookie) {
      config.headers['wfmCookie'] = `${wfmCookie}`;
      // config.headers['wfmCookie'] = `${jsessionId}`;
      // config.headers.Authorization = `${jsessionId}`;
    }
    return config;
  },
  error => Promise.reject(error)
);

root.render(
  <Provider store={store}>
    <App />
  </Provider>
);

