import {
    BrowserRouter,
    Navigate,
    Outlet,
    Route,
    Routes,
} from 'react-router-dom';
import { HomePage } from '../home';
import { LoginPage } from '../login';
import { AgentRequestList } from '../pages/agent-request-list';
import { AgentTimeOffDay } from '../pages/agent-time-off-day';
import { MyScheduleChanges } from '../pages/my-schedule-changes';
import { WeeklySchedule } from '../pages/weekly-schedule';
import { useDispatch, useSelector } from 'react-redux';
import { useEffect } from 'react';
import { initSession } from '../store/sessionUserReducer';
import { PrivateRoute } from './private-routing';


export const AppRouter: React.FC = () => {
    const dispatch = useDispatch();
    const session = useSelector((state: any) => state);

    console.log(session)

    useEffect(() => {
        dispatch(initSession() as any);
        // console.log(user, 'user');
    }, [dispatch]);

    // if(!session?.isPageLoaded){
    //     return (
    //         <p>Loading...</p>
    //     )
    // }


    return (
        // <BrowserRouter basename="/wfm/web">
        <BrowserRouter basename={`${process.env.REACT_APP_API_BASENAME}`} future={{ v7_startTransition: true }}>
            <Routes>
                <Route path="/" element={<Outlet />}>
                    <Route index element={<Navigate to={'/login'} />} />
                    <Route
                        path="/login"
                        element={<LoginPage />}
                    />
                    <Route
                        path="app"
                        element={
                            <PrivateRoute isAuthenticated={session?.isAuthenticated} />
                        }
                    >

                    </Route>
                    <Route path="/home" element={<HomePage />} />
                    <Route path="/weekly-schedule" element={<WeeklySchedule />} />
                    <Route path="/agent-request-list" element={<AgentRequestList />} />
                    <Route path="/agent-time-off-day" element={<AgentTimeOffDay />} />
                    <Route path="/my-schedule-changes" element={<MyScheduleChanges />} />
                </Route>
            </Routes>
        </BrowserRouter>
    );
};
